import {ViewChild} from '@angular/core';
import {Component} from '@angular/core';
import {Hauler} from '@models/business/hauler.model';
import {Order} from '@models/business/order.model';
import {OrderGroup} from '@models/order-helper/order-group.model';
import {OrderView} from '@models/order-helper/order-view.model';
import {UserSettings} from '@models/settings/settings.model';
import {DateTimeCombinationEnum} from '@models/units-and-format/date-format.enum';
import {OrderDetailTabsPage} from '@app/pages/order-detail-tabs/order-detail-tabs.page';
import {DatePipe} from '@app/pipes/date-pipe/date.pipe';
import {LoadingController} from '@ionic/angular';
import {ModalController} from '@ionic/angular';
import {IonDatetime} from '@ionic/angular';
import {Transition} from '@models/workflow/transition.model';
import {MblsAnalyticsService} from '@services/mbls-analytics-service/MblsAnalyticsService';
import {LogService} from '@services/log/log.service';
import {MobileContextService} from '@services/mobile-configuration-service/mobile-context.service';
import {OrderService} from '@services/order-service/order.service';
import {OrderSyncService} from '@services/order-sync/order-sync.service';
import * as moment from 'moment';
import {Subject} from 'rxjs';
import {combineLatest} from 'rxjs';
import {takeUntil} from 'rxjs/operators';

const CONFIG = {
  GA: {
    PAGE_NAME: 'OrderHistoryPage',
  }
};

@Component({
  selector: 'app-order-history',
  templateUrl: './order-history.page.html',
  styleUrls: ['./order-history.page.scss'],
})
export class OrderHistoryPage {

  @ViewChild('datePicker', {static: true})
  public datePicker: IonDatetime;
  public allOrders: Array<OrderView> = [];
  public hauler: Hauler;
  public orderGroups: Array<OrderGroup> = [];
  public activeOrders: Array<OrderView> = [];
  public deliveryDate: string = moment().format('YYYY-MM-DD');
  public daysInThePast = 0;
  public minDate: string = moment().subtract(this.daysInThePast, 'd').format('YYYY-MM-DD');
  public maxDate: string = moment().add(365, 'd').format('YYYY-MM-DD');
  protected _component: OrderHistoryPage;
  private loading: any;
  userSettings: UserSettings = new UserSettings();
  private unsubscribe: Subject<void> = new Subject<void>();
  public displayFormat: string = moment().format('YYYY-MM-DD');
  public datePickerDisplayFormat = 'YYYY/MM/DD';
  dateType: DateTimeCombinationEnum = DateTimeCombinationEnum.dateOnly;
  datePickerType = true;

  constructor(private modalCtrl: ModalController,
              private orderService: OrderService,
              private loadingCtrl: LoadingController,
              private orderSyncService: OrderSyncService,
              private log: LogService,
              private mobileContextService: MobileContextService,
              private ga: MblsAnalyticsService,
              private datePipe: DatePipe,
  ) {
    this.orderSyncService.sendPaymentUpdate().subscribe(orderChange => {
      if (orderChange as Order) {
        const orderView = this.activeOrders.find(oV => oV.order.id === orderChange.id);
        if (orderView) {
          orderChange.uuid = orderView.order.uuid;
          orderView.order = orderChange;
        }
      }
    });

    this.mobileContextService.userSettingsObservable
      .pipe(
        takeUntil(this.unsubscribe)
      )
      .subscribe((userSettings) => {
        this.userSettings = userSettings;
      });
  }

  // noinspection JSUnusedGlobalSymbols
  ionViewDidEnter() {
    this.ga.trackView(CONFIG.GA.PAGE_NAME).catch(error => this.log.info(`Unable to track view ${CONFIG.GA.PAGE_NAME} with GA`, error));
    this.getOrders(this.deliveryDate);
  }

  /***
   * get orders by their delivery dates
   */
  getOrders(deliveryDate: string) {
    this.loading = this.loadingCtrl.create()
      .then(async (res) => {
          await res.present();
          combineLatest([
            this.orderSyncService.getDatedOrders(moment(deliveryDate).format('YYYY-MM-DD')),
            this.mobileContextService.haulerObservable
          ])
            .subscribe(([orders, hauler]) => {
                this.allOrders = [];
                this.orderGroups = [];
                this.activeOrders = [];
                orders.forEach(order => {
                  this.allOrders.push(this.orderService.constructOrderView(order));
                });
                this.hauler = hauler;
                this.activeOrders = this.sortOrders(this.allOrders, 'tenantName');
                this.manageGroups(this.allOrders);
                this.canHaulerSeeFutureOrder();
                res.dismiss();
              }, err => {
                res.dismiss();
                this.log.error(err);
              }
            );
        }
      );
  }

  showGroupHeader = (record: OrderView, recordIndex: number, records: Array<OrderView>) => {
    try {
      if (recordIndex === 0 || (record.order.tenantName !== records[recordIndex - 1].order.tenantName)) {
        if (this.orderGroups) {
          const group = this.orderGroups.find(value => value.name === record.order.tenantName);
          if (group) {
            return group;
          }
        }
      }
    } catch (e) {

    }

    return null;
  }

  identify(index, orderView: OrderView) {
    return orderView.order.uuid.toString();
  }

  toggleGroupView(groupName: string) {
    const orderGroup = this.orderGroups.find(oG => oG.name === groupName);
    if (orderGroup) {
      if (orderGroup.hidden) {
        this.activeOrders = this.activeOrders.filter(orderView => orderView.order.tenantName !== groupName);
      } else {
        this.activeOrders = this.activeOrders.concat(this.allOrders.filter(orderView => orderView.order.tenantName === groupName));
      }
      orderGroup.hidden = !orderGroup.hidden;
      orderGroup.visible = !orderGroup.visible;
    }
  }

  showTimePicker() {
    this.datePicker.open();
  }

  async selectItem(event, orderView) {
    const itemModal = await this.modalCtrl.create({
      id: 'order-detail-tabs-page',
      component: OrderDetailTabsPage,
      componentProps: {
        orderView: orderView,
        isforHistory: true
      }
    });
    itemModal.present();
    itemModal.onDidDismiss().then(
      // (data: { order: Order }) => {
      (eventDetail) => {
        if (eventDetail && eventDetail.data && eventDetail.data.order) {
          const transition: Transition = eventDetail.data.transition;
          const view = this.allOrders.find(oV => oV.order.id === eventDetail.data.order.id);
          if (view && transition) {
            view.order.local = false;
            view.order.status = transition.to.name;
          }
        }
      }
    );
  }

  /**
   * Sort orders
   *
   */
  sortOrders(orders: Array<OrderView>, prop: string): Array<OrderView> {
    const result = [];
    const ordersSorted = orders.reduce((groups, item) => {
      const val = item.order[prop];
      groups[val] = groups[val] || [];
      groups[val].push(item);
      return groups;
    }, {});
    Object.keys(ordersSorted).forEach(item => {
        result.push(...ordersSorted[item]);
      }
    );
    return result;
  }

  canHaulerSeeFutureOrder() {
    if (this.hauler) {
      this.orderGroups.forEach(orderGroup => {
        const provider = this.hauler.linkedProviders.find(p => p.name === orderGroup.name);
        const canSeeFutureOrders = !moment(this.deliveryDate).isAfter(moment()) ||
          (provider.configuration && provider.configuration.allowFutureViewForHauler);
        orderGroup.canHaulerSeeFutureOrders = canSeeFutureOrders;

        orderGroup.hidden = !canSeeFutureOrders;
        if (!canSeeFutureOrders) {
          this.toggleGroupView(orderGroup.name);
        }
      });
    }
  }

  isDatePickerVisible() {
    if (this.hauler) {
      return this.hauler.linkedProviders.some(provider => provider.configuration.allowFutureViewForHauler);
    }
  }

  private manageGroups(orders: Array<OrderView>) {
    if (this.hauler) {
      this.hauler.linkedProviders.forEach(provider => {
        const orderGroup = new OrderGroup(provider.name);
        this.orderGroups.push(orderGroup);
      });
      orders.forEach(orderView => {
        const orderGroup = this.orderGroups.find(oG => oG.name === orderView.order.tenantName);
        if (orderGroup) {
          orderGroup.orders.push(orderView);
        }
      });
    }
  }

  changeDisplayFormat(deliveryDate) {
    this.displayFormat = this.datePipe.transform(deliveryDate, this.userSettings.hourFormat, this.userSettings.dateFormat, this.dateType);
  }

  ionViewDidLeave() {
    this.unsubscribe.next();
  }

  ionViewWillUnload() {
    this.unsubscribe.complete();
  }
}
